      <span onclick="window.history.go(-1)"></span>
      <span>京东秒杀</span>
      <span>...</span>
    </div>
    <div class="seckill_main">
      <div class="recomend">
        <div class="title_recomend">
          <span>品牌闪购</span>
          <span>精选优选特卖</span>
          <span>每日特价</span>
          <span>超低价好货</span>
        </div>
        <div class="shop_recomend">
          <router-link to="" v-for="item in shop_seckill.recomend" :key="item.id">
            <img :src="item.product_img_url" alt="">
            <p>¥{{item.product_price}}</p>
          </router-link>
        </div>
      </div>
      <div class="time">
        <router-link to=""><p>10:00</p><p>抢购中</p></router-link>
        <router-link to=""><p>12:00</p><p>即将开始</p></router-link>
        <router-link to=""><p>13:00</p><p>即将开始</p></router-link>
        <router-link to=""><p>14:00</p><p>即将开始</p></router-link>
        <router-link to=""><p>15:00</p><p>即将开始</p></router-link>
      </div>
      <div class="shop_seckill">
        <div
        class="item_shop_seckill" v-for="item in recommendshoplist" :key="item.id"
        @click.prevent="todetail(item.goods_id, item.image_url, item.short_name, item.price, item.normal_price)"
        >
          <img :src="item.image_url" alt="">
          <div class="meta_item">
            <p>{{item.short_name}}</p>
            <div class="text">
              <span>自营</span>
              <span>享延迟送货补贴</span>
            </div>
            <div class="price_item">
              <div class="price">
                <span>¥{{item.price}}</span>
