            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="seckill_footer">
      <router-link to="" class="tabItem tab-item-link">
        <img src="" alt="" class="tab-item-icon">
      </router-link>
      <router-link to="" class="tabItem tab-item-link">
        <img src="../../assets/images/n-find.png" alt="" class="tab-item-icon">
      </router-link>
      <router-link to="" class="tabItem tab-item-link">
        <img src="../../assets/images/n-cart.png" alt="" class="tab-item-icon">
      </router-link>
      <router-link to="" class="tabItem tab-item-link">
        <img src="../../assets/images/n-me.png" alt="" class="tab-item-icon">
      </router-link>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  data () {
    return {
      // shop_recomend: [],
      shop_seckill: []
    }
  },
  mounted () {
    // console.log(this.$http.get('/recommendshoplist'), 11111111111)
    // console.log(this.$store.state.recommendshoplist, 1234566666666666666666666)
    this.$store.dispatch('reqRecommendShopList')
  },
  computed: {
    ...mapState(['recommendshoplist', 'userInfo'])
  },
  methods: {
    todetail (goodsid, imageurl, shortname, price, normalprice) {
      // alert(goodsid, imageurl, shortname, price, normalprice)
      this.$router.push({
        name: 'Detail',
        params: {
          goods_id: goodsid,
          thumb_url: imageurl,
          goods_name: shortname,
          price: price
        }
      })
    },
    // todetail (goodsid, imageurl, shortname, price, normalprice) {
    //   alert(123)
    //   this.$router.push({
    //     name: 'Detail',
    //     params: {
    //       id: 123,
    //       image_url: 456,
    //       short_name: 789
    //     }
    //   })
    // },
    getData () {
      this.$http.get('/shop_seckill').then((res) => {
        this.shop_seckill = res.data
        // console.log(res.data.recomend)
      })
    }
  }
}
</script>
<style lagn="less" scoped>
  @import '../../assets/css/seckill.css';
</style>
